.nav {
  &-list {
    display: flex;
    align-items: center;
    gap: 0 40px;

    margin: 0;
    padding: 0;
    list-style: none;
  }
  &-item {
    &__link {
      font-weight: 500;
      line-height: 21px;
      text-decoration: none;
      color: var(--color-main);
    }
  }
}
